.connect {
  padding: 4rem;
  background: white;
  font-size: 40px;
  }

.connect {
    background-image: url('/public/assets/connect/connect_with_us.jpg');
    background-position: center;
    background-size: fill;
    background-repeat: no-repeat;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
    color: #fff;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.social-icons {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

.facebook {
  padding: 15px 15px;
}

.instagram {
  padding: 15px 15px;
}

.youtube {
  padding: 15px 15px;
}

.linkedIn {
  padding: 15px 15px;
}

.whatsapp {
  padding: 15px 15px;
  text-decoration: none;
}
.whatsapp-icon {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.connect > h1 {
  color: #fff;
  text-align: center;
  margin-bottom: 40px;
  transition:500ms;
}

.subText{
  color: #ffffff;
  font-size: 40px;
  text-align: center;
  transition:500ms;
}


.connect > h3 {
  color: #fff;
  text-align: center;
  margin-bottom: 40px;
  transition:500ms;
  font-size: 40px;
}

  .linkedIn:hover{
    color: #39b9fa;
    transform: scale(1.5);
  }

  .whatsapp:hover{
    color: #03ff2d;
    transform: scale(1.5);
  }

.instagram:hover{
  color: #ff61ad;
  transform: scale(1.5);
  transition:500ms;
}

.youtube:hover{
  color: #ff0000;
  transform: scale(1.5);
  transition:500ms;
}

.twitter:hover{
  color: #0061bc;
  transform: scale(1.5);
  transition:500ms;
}

.linkedIn:hover{
  color: #39b9fa;
  transform: scale(1.5);
  transition:500ms;
}

.whatsapp:hover{
  color: #03ff2d;
  transform: scale(1.4);
  transition:500ms;
}

@media only screen and (max-width: 800px) {
  .connect > h1 {
    color: #fff;
    text-align: center;
    margin-bottom: 40px;
    transition:500ms;
    font-size: 40px;
  }
  .connect > h3 {
    color: #fff;
    text-align: center;
    margin-bottom: 40px;
    transition:500ms;
    font-size: 20px;
  }
}

@media only screen and (max-width: 500px) {
  .whatsapp-icon , h2 {
    font-size: 20px;
  }
}
